@import '../../../variables.scss';

.button {
  font-size: 0.875rem;
  height: 40px;
  line-height: 40px;
  text-transform: uppercase;
  width: 100%;
  border: 1px solid $main-color-btn;
  transition: background-color 0.3s, border-color 0.3s, color 0.3s;
  &:hover {
    cursor: pointer;
  }
}
